import React,{useState} from 'react'
import {useHistory} from "react-router-dom"
import Axios from "axios"
const Add = () => {
    let history = useHistory()
    const [user, setUser] = useState({
        name:"",
        username:"",
        email:"",
        phone:"",
        website:"",
    })
    const {name,username,email,phone,website} = user;
    
    const eventOchange = async e     =>{
    //    console.log(e.target.value) 
        setUser({...user, [e.target.name]:e.target.value})
    }
    const onSubmit = async e =>{
        e.preventDefault();
        await Axios.post("http://localhost:3003/user", user);
        history.push('/');
        setUser([]);

    }
    return (
        <div className='addUser'>
            <div className="centers shadow">
                <h1 className='text-center'>Add User</h1>
                <div className="form_area">
                    <form onSubmit={onSubmit}>
                        <div className="mb-3">
                            <label htmlFor="name" className="form-label">Enter Your Name</label>
                            <input type="text" className="form-control" id="name" name="name" value={name} onChange={e=> eventOchange(e)}/>
                        </div>
                        <div className="mb-3">
                            <label htmlFor="username" className="form-label">Enter Your Username</label>
                            <input type="text" className="form-control" id="username" name="username" value={username}  onChange={e => eventOchange(e)}/>
                        </div>
                        <div className="mb-3">
                            <label htmlFor="email" className="form-label">Enter Your Email Address</label>
                            <input type="email" className="form-control" id="email" name="email" value={email}  onChange={e=> eventOchange(e)}/>
                        </div>
                        <div className="mb-3">
                            <label htmlFor="phone" className="form-label">Enter Your Phone Number</label>
                            <input type="phone" className="form-control" id="phone" name="phone" value={phone}  onChange={e=> eventOchange(e)}/>
                        </div>
                        <div className="mb-3">
                            <label htmlFor="websites" className="form-label">Enter Your Website Name</label>
                            <input type="password" className="form-control" id="websites" name="website" value={website}  onChange={e=> eventOchange(e)}/>
                        </div>
                        
                        <button type="submit" className="btn btn-primary d-block m-auto" >Submit</button>
                    </form>
                </div>
            </div>
        </div>
    )
}

export default Add
