import React from 'react'
import './scss/main.css'
import Home from './component/pages/Home'
import About from './component/pages/About'
import Contact from './component/pages/Contact'
import Navbar from './component/layout/Navbar'
import Add from './component/users/Add'
import View from './component/users/View'
import Edit from './component/users/Edit'
// import View from './component/users/View'
import ErrorPage from './component/pages/ErrorPage'
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom'

const App = () => {
  return (
    <>
      <Router>
        <Navbar />
        <Switch>
          <Route exact path='/' > <Home/> </Route>
          <Route exact path='/About'  component={About}/>
          <Route exact path='/Contact' component={Contact} />
          <Route exact path='/users/add' component={Add} />
          {/* <Route exact path='/users/View' component={View} /> */}
          <Route exact path='/users/Edit/:id' component={Edit} />
          <Route exact path='/users/:id' component={View} />
          <Route><ErrorPage/></Route>
        </Switch>
      </Router>
    </>
  )
}

export default App
