import React, { useState, useEffect } from 'react';
import Axios from "axios"
import { Link } from 'react-router-dom'


const Home = () => {
    const [users, setUsers] = useState([])

    useEffect(() => {
        // console.log("h r u ?")
        loadUsers();
    }, [])
    const loadUsers = async () => {
        const result = await Axios.get("http://localhost:3003/user")
        // console.log(result.data );
        setUsers(result.data)
    }
    const deletUser = async id =>{
        await Axios.delete(`http://localhost:3003/user/${id}`);
        loadUsers();
    }
    return (
        <div className='Home'>
            <div className="container">
                <h1 className="">Curd Data</h1>
                <table className="table border shadow table-bordered">
                    <thead className="table-dark">
                        <tr>
                            <th scope="col">#</th>
                            <th scope="col">Name</th>
                            <th scope="col">User Name</th>
                            <th scope="col">Email</th>
                            <th>Action</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            users.map((user, index) => (
                                <tr key={user.id}>
                                    <th scope="row">{index + 1}</th>
                                    <td>{user.name}</td>
                                    <td>{user.username}</td>
                                    <td>{user.email}</td>
                                    <td >
                                        <Link className="btn btn-outline-primary"  to={`/users/${user.id}`}> <i className="far fa-eye"></i></Link>
                                        <Link className='btn btn-outline-primary mx-3' to={`/users/Edit/${user.id}`}> <i className="fas fa-edit" ></i></Link>
                                        <Link className='btn btn-outline-danger' onClick={()=> deletUser(user.id)}> <i className="fas fa-trash-alt"></i></Link>
                                    </td>
                                </tr>
                            ))
                        }
                    </tbody>
                </table>
            </div>
        </div>
    )
}

export default Home
